import Vue from 'vue'
import Vuex from 'vuex'
import products from "@/data/product-database"
import Localbase from 'localbase'

let db = new Localbase('db')
db.config.debug = false


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    drawer: false,
    products: products,
    cart: [],
  },
  getters: {
    getCartProducts: state => {
      return state.cart.map(({ id, quantity }) => {
        const product = state.products.find(p => p.id === id)
        return {
          name: product.name,
          price: product.price,
          quantity
        }
      })
    },
    getCartTotalItems: state => state.cart.reduce((total, item) => total + item.quantity, 0),
    getCartTotalCost: state => state.cart.reduce((total, item) => total + (item.price * item.quantity), 0),
    getCategories: state => state.categories,
    getProducts: state => state.products.slice(0, 10),
  },
  mutations: {
    setDrawer: (state, payload) => (state.drawer = payload),
    toggleDrawer: state => (state.drawer = !state.drawer),

    addToCart(state, content) {
      const product = state.products.find(prod => prod.id === content.id)
      const item = state.cart.find(p => p.id === content.id)
      if (item) {
        item.quantity++
      } else {
        state.cart.push({
          id: product.id,
          title: product.title,
          price: product.price,
          imgUrl: product.imgUrl,
          category: product.category,
          quantity: 1
        })
      }
    },
    deleteFromCart(state, id) {
      state.cart = state.cart.filter(cartItem => cartItem.id !== id)
    },
    addItemAlreadyInCart(state, id) {
      if (state.cart.find(cartItem => cartItem.id === id)) {
        state.cart.find(cartItem => cartItem.id === id).quantity++
        console.log(state.cart)
        return [...state.cart]
      }

    },
    subtractItemAlreadyInCart(state, id) {
      if (state.cart.find(cartItem => cartItem.id === id)) {
        if (state.cart.find(cartItem => cartItem.id === id).quantity > 1) {
          state.cart.find(cartItem => cartItem.id === id).quantity--
          return [...state.cart]
        } else if (state.cart.find(cartItem => cartItem.id === id).quantity === 1) {
          state.cart = state.cart.filter(cartItem => cartItem.id !== id)
        } else {
          console.log("Error: Item quantity is 0")
        }
      }
    },
    addToFavorites(state, id) {
      state.products.find(productItem => productItem.id === id).isFavorite = true
    },
    removeFromFavorites(state, id) {
      state.products.find(productItem => productItem.id === id).isFavorite = false
    }
    
  },
  actions: {
    addItemToCart({ commit }, content) {
      commit('addToCart', content)
    },
    addItemAlreadyInCart({ commit }, id) {
      commit('addItemAlreadyInCart', id)
    },
    subtractItemAlreadyInCart({ commit }, id) {
      commit('subtractItemAlreadyInCart', id)
    },
    deleteFromCart({ commit }, id) {
      commit('deleteFromCart', id)
    },
    addToFavorites({ commit }, id) {
      commit('addToFavorites', id)
    },
    removeFromFavorites({ commit }, id) {
      commit('removeFromFavorites', id)
    },

    // addItemToCart({ state, commit }, content) {
    //   const product = state.products.find(prod => prod.id === content.id)
    //   const item = state.cart.find(p => p.id === content.id)

    //   if (item) {
    //     item.quantity++
    //   } else {
    //     state.cart.push({
    //       id: product.id,
    //       title: product.title,
    //       price: product.price,
    //       imgUrl: product.imgUrl,
    //       category: product.category,
    //       quantity: 1
    //     })
    //   }
    //   db.collection('cart').add(content).then(()  => {
    //     commit('addToCart', content)
    //   })
    // },
  },
})

